import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {NavParams} from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import {ConfigService} from './../config.service';
import { DecimalPipe } from '@angular/common';
@Component({
  selector: 'app-detail-elaborado',
  templateUrl: './detail-elaborado.page.html',
  styleUrls: ['./detail-elaborado.page.scss'],
})

export class DetailElaboradoPage implements OnInit {
  
  public selectItem ;
  public Subitems;
  public prima;
  public cant;
  public itemsP;
  public itemsPcopy;
  public search = "";
  constructor(private host:ConfigService,private decimalPipe: DecimalPipe,private http: HttpClient,private params:NavParams,private modal: ModalController) {
    if(params.data.items!=null){
      this.selectItem = params.data.items;
    }else{
      this.selectItem = {Id:null,Nombre: "",costoIndirecto: 0,ganacia:0,subtotal:0,total:0};
    }
    this.http.get<any>(this.host.getHost() + '/selectPrima').subscribe(data => {
      this.itemsPcopy = data;
      this.prima = data[0]
    })
    
    this.Subitems = [];
    this.cant =0;
    this.ngOnInit();
   }
   twoDecimals(number) {
    return this.decimalPipe.transform(number, '1.3-3');
  }
   selectSearch(item){
    this.prima = item;
    this.itemsP = [];
   }
   searchMaterial(){
    this.itemsP = [];
    if(this.search!=''){
      this.itemsPcopy.forEach(element => { 
        var pos = element.Nombre.toUpperCase().search(this.search.toUpperCase())
        if(pos>-1)
        {
          this.itemsP.push(element);
        }
      });
    }
   }
   navBack(){

    this.modal.dismiss({
      'dismissed': true
    })
  }
  addItem(){
    this.prima.IdElaborada = this.selectItem.Id;
    this.prima.value  = this.cant;
    var item = new Object(this.prima);
    this.itemsP = [];
    if(this.selectItem.Id!=null){
      this.http.post<any>(this.host.getHost() + '/InsertElaboradaDetail',item).subscribe(data => {
       
      })
    }
    this.prima = ""
    this.Subitems.push(item);
  }
  changeItem(subitem){
    if(this.selectItem.Id!=null){
      this.http.post<any>(this.host.getHost() + '/updateElaboraDetail',subitem).subscribe(data => {
        this.ngOnInit()
      })
    }
  }
  
  insertName(){
    var item = this.selectItem;
    if(this.selectItem.Id==null){  
      item.produc = this.Subitems;
      this.http.post<any>(this.host.getHost() + '/InsertElaborada',item).subscribe(data => {
        this.selectItem = data[0];
      })
    }
  }
  save(){
    var item = this.selectItem;
    if(this.selectItem.Id!=null){  
      this.http.post<any>(this.host.getHost() + '/UpdateElabora',item).subscribe(data => {
        this.ngOnInit()
      })
    }
    
  }
  DeleteItem(subitem){
    if(this.selectItem.Id!=null){
      this.http.post<any>(this.host.getHost() + '/DeleteElaboraDetail',subitem).subscribe(data => {
        this.ngOnInit()
      })
    }
  }
  ngOnInit() {
    if(this.selectItem.Id!=null){  
      this.http.post<any>(this.host.getHost() + '/selectElaboraID',this.selectItem).subscribe(data => {
        this.selectItem = data[0];
      })
      this.http.post<any>(this.host.getHost() + '/selectDetallE',this.selectItem).subscribe(data => {
          this.Subitems = data;
      })
    }
    
  }

}
