import { Component,OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import { DetailElaboradoPage } from '../detail-elaborado/detail-elaborado.page';
import { element } from 'protractor';
import {ConfigService} from './../config.service'
import { material } from './../shared/material';
@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss']
})
export class Tab2Page implements OnInit{
  public items;
  public selectItem;
  pushPage: any;
  filter: material = new material();
  constructor(private host:ConfigService,private http: HttpClient,private modal: ModalController) { 
      
   
  }
  ngOnInit(){
    this.http.get<any>(this.host.getHost() + '/selectElabora').subscribe(data => {
      this.items = data;
    })

  }
  async itemSelected(item: string) {
    let modal = await this.modal.create({
      component: DetailElaboradoPage,
      componentProps: {
        models:this.items,
        items:item  
      }
    });
    modal.present();

    return await modal.onDidDismiss().then(()=>{
      this.http.get<any>(this.host.getHost() + '/selectElabora').subscribe(element => {
        this.items = element;
        console.log(this.items)
      })
    });
  }
  async addItem() {
    const modal = await this.modal.create({
      component: DetailElaboradoPage,
      componentProps: {
        items:null,
      },
      
    });
    modal.onDidDismiss().then(()=>{
      this.http.get<any>(this.host.getHost() + '/selectElabora').subscribe(element => {
        this.items = element;
        console.log(this.items)
      })
    });
    return await modal.present();
  }
}
